<template>
<div>
  <CCardBody>
    <CDataTable
      :items="formatItems"
      :fields="fields"
      :items-per-page="perPage"
      column-filter
      hover
      :sorter="sorter"
      @page-change="onPageChange"
      @update:sorter-value="onSorterValue"
      @update:column-filter-value="onColumnFilterValue"
    >
    <template #over-table>
      <CButton class="btn btn-primary" @click="showModal">新增</CButton> 
    </template>
      <template #status="{item}">
        <td>
          <CBadge :color="getBadge(item.status)">
            {{item.status}}
          </CBadge>
        </td>
      </template>


      <template #action="{item, index}">
        <td class="py-2">
          <CButton
            color="primary"
            variant="outline"
            square
            size="sm"
            @click="editBusinessCode(item, index)"
          >
            编辑
          </CButton>
          <CButton
            color="primary"
            variant="outline"
            square
            size="sm"
            @click="deleteBusinessCode(item, index)"
          >
            删除
          </CButton>
        </td>
      </template>

      <template #under-table="">

    <CPagination
      v-show="pages>1"
      :activePage.sync="activePage"
      :pages="pages"
      @my-page-change="onPageChange"
    />
    </template>

    </CDataTable>
  </CCardBody>



<!----------modal------------->


  <CModal
      title="编辑/新增 业务编码"
      color="primary"
      :show.sync="openModal"
    >
<template>
  <div>
        <CRow>
      <CCol sm="6">
        <CSelect
          label="模块"
          :options="Object.values(moduleSelectOptionMap)"
          :value.sync="model.moduleCode"
        />
      </CCol>
      <CCol sm="6">
        <CSelect
          label="类型"
          :options="Object.values(typeSelectOptionMap)"
          :value.sync="model.typeCode"
        />
      </CCol>
    </CRow>
    <CRow>
      <CCol sm="12">
        <CInput
          label="标题"
          placeholder="建议见名知意"
          :value.sync="model.title"
        />
      </CCol>
    </CRow>
    <CRow>
      <CCol sm="12">
        <CInput
          label="常量符号"
          placeholder="建议大写"
          :value.sync="model.name"
        />
      </CCol>
    </CRow>
        <CRow>
      <CCol sm="12">
        <CTextarea
          label="说明"
          placeholder="业务错误码简要说明"
          :value.sync="model.description"
        />
      </CCol>
    </CRow>

  </div>
</template>
<template #footer="">
  <button
                  type="button"
                  class="btn btn-secondary" 
                  @click="hideModal"
                >
                  Cancel
                </button>
                <button
                  type="button"
                  class="btn btn-primary"
                  @click="saveBusinessCode"
                >
                  OK
                </button>
</template>
    </CModal>

<!---------------cover--------------------->

<CElementCover  v-show="ajaxDoing"
        :boundaries="[{ sides: ['top', 'left']}]"
        :opacity="0.8"
      >
        <h1 class="d-inline">Loading... </h1><CSpinner size="5xl" color="success"/>
      </CElementCover>


</div>
</template>



<script>

const fields = [
  {key:"id",label:"ID",filter:false},
  {key:"moduleCode",label:"模块",sorter:false},
  {key:"typeCode",label:"类型",sorter:false},
  {key:"title",label:"标题",sorter:false},
  {key:"code",label:"数字代码"},
  {key:"name",label:"常量表达式",sorter:false},
  {key:"description",label:"说明",sorter:false},
  {key:"status",label:"状态"},
  { 
    key: 'action', 
    label: '操作', 
    sorter: false, 
    filter: false
  }
]



const backendApi=(uri)=>{
  return `${host}${uri}`;
}
import axios from 'axios';
import { thisExpression } from '@babel/core/node_modules/@babel/types';

export default {
  name: 'CodeList',
  data () {
    return {
      sorter:{ external: true, resetable: true },
      activePage:1,
      pages:1,
      perPage:5,
      sorterMap:{// 排序

      },
      filterMap:{// 筛选

      },
      ajaxDoing:false,
      openModal:false,
      items:[],
      formatItems:[],// 格式化显示数据
      moduleSelectOptionMap:{},
      typeSelectOptionMap:{},
      model:{
                id:0,
                moduleCode:"",
                typeCode:"",
                name:"",
                title:"",
                code:"",
                description:"",
                status:"",
                createTime:"",
                updateTime:""
            },
      fields,
      details: [],
      collapseDuration: 0
    }
  },
  watch:{
    activePage (val) {
      this.onPageChange(val)
    }
  },
  computed:{
  },

  
  mounted: function () {
    axios.get(`${host}/api/v1/module/all`).then((response) => {
      const optionMap={}
        response.data.forEach(item=>{
          optionMap[item.code]={label:item.title,value:item.code}
          });
          this.moduleSelectOptionMap=optionMap
          const codeList = Object.keys(optionMap);
          this.model.moduleCode= codeList.length>0&&codeList[0];// 默认选中第一个
    });
    axios.get(`${host}/api/v1/type/all`).then((response) => {
      const optionMap={}
      response.data.forEach(item=>{
          optionMap[item.code]={label:item.title,value:item.code}
          });
      this.typeSelectOptionMap=optionMap
      const codeList = Object.keys(optionMap);
      this.model.typeCode= codeList.length>0&&codeList[0];// 默认选中第一个
    });
    
    this.getList();
                },
  methods: {
    formatRow:function(row){// 格式化显示
      const formatRow = {...row};// 复制一份，不改变原来的
      const moduleCode = row["moduleCode"];
      const moduleRow= this.moduleSelectOptionMap[moduleCode];
      if (moduleRow){
        formatRow["moduleCode"]=moduleRow["label"]+"("+moduleRow["value"]+")";
      }

      const typeCode = row["typeCode"];
      const typeRow= this.typeSelectOptionMap[typeCode];
      if (typeRow){
        formatRow["typeCode"]=typeRow["label"]+"("+typeRow["value"]+")";
      }
      return formatRow;
    },
    hideModal:function(e){
    this.openModal=false;
  },
  showModal(){
this.openModal=true;// 打开modal;
  },
  queryString(map){
    const all ={};
    
    const paginationMap={
      size:this.perPage,
      page:this.activePage
    }
    const tmpSorterMap=this.sorterMap
    const sorterMap ={}
    const arr=[]
    Object.keys(tmpSorterMap).forEach(column=>{
      const value = tmpSorterMap[column]
      value && arr.push(column+" "+value)
    });

    arr.length>0 && (sorterMap["order"]=arr.join(","))

    Object.assign(all,this.filterMap,sorterMap,paginationMap,map)
    return Object.keys(all).map((key) => {
    return encodeURIComponent(key) + '=' + encodeURIComponent(all[key])
}).join('&');
  },
  // 翻页
  onPageChange(page){
    this.activePage=page;
    this.getList();

  },
  onSorterValue(observer){
    const {column,asc}=observer;
    if (column){
      const value =asc===true?"asc":(asc===false?"desc":null)
      if (value){
        this.sorterMap[column]=value
      }else{// 值为null时删除条件
        delete this.sorterMap[column]
      }
    }
    this.getList();

  },
  onColumnFilterValue(observer){
    const component = this
    Object.keys(observer).forEach(column=>{
      component.filterMap[column]=observer[column]
    });
    this.getList();
  },





  getList(){
    const query=this.queryString()
    this.ajaxDoing=true;
    axios.get(`${host}/api/v1/code/list?${query}`).then((response) => {

      const {items,pagination:{size,page,total}}=response.data
        this.items=items
        this.formatItems=items.map(this.formatRow)
        this.perPage=size,
        this.activePage=page,
        this.pages=Math.ceil(total/size)

        
        this.ajaxDoing=false;
    });
  },
    saveBusinessCode() {
      this.ajaxDoing=true;
      const model = this.model
      const url = model.id?backendApi('/api/v1/code/update'):backendApi('/api/v1/code/create');
      const method = model.id?"put":"post";
      console.log("create",model)
      axios[method](url,model).then((response) => {
        this.ajaxDoing=false;
        this.hideModal();
        location.reload();
      });
      return false;

  },
  editBusinessCode(item) {
    this.showModal();
      const id = item.id;
      const url = backendApi(`/api/v1/code/get?id=${id}`)
      axios.get(url).then((response) => {
          this.model = response.data;
          this.ajaxDoing=false;
      });
      return false;
  },
  deleteBusinessCode(item) {
    this.ajaxDoing=true;
    const id = item.id;
    const url = backendApi(`/api/v1/code/delete?id=${id}`)
    axios.delete(url).then((response) => {
      this.ajaxDoing=false;
      location.reload();
    });
    return false;
  },
    getBadge (status) {
      switch (status) {
        case 'ON': return 'success'
        case 'OFF': return 'warning'
        default: 'primary'
      }
    }
  }
}
</script>
